<template>
  <v-card class="pa-4">
    <h2>Closed Assignments by Adjuster (By Month)</h2>
    <v-row dense>
      <v-col cols="12" md="4">
        <v-select
          v-model="selectedBranch"
          :items="branches"
          item-text="name"
          item-value="id"
          label="Branch"
          dense
          required
        />
      </v-col>
      <v-col cols="12" md="4">
        <v-select
          v-model="selectedAdjuster"
          :items="filteredAdjusters"
          item-text="username"
          item-value="id"
          label="Adjuster"
          dense
          required
          :disabled="!selectedBranch"
        />
      </v-col>
      <v-col cols="12" md="4">
        <v-menu v-model="menuMonth" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedMonth"
              label="Month"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedMonth"
            type="month"
            @change="menuMonth = false"
          />
        </v-menu>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <v-btn
      color="success"
      class="mt-2"
      :disabled="!selectedAdjuster || !selectedMonth"
      @click="exportExcel"
      :loading="exporting"
    >
      <v-icon left>mdi-file-excel</v-icon>
      Export Closed Assignments
    </v-btn>
    <v-alert type="info" outlined class="mt-4">
      ðŸš§ This feature generates a styled Excel sheet. Table preview coming soon!
    </v-alert>
  </v-card>
</template>

<script>
import moment from "moment";
import BranchDataService from "../../services/BranchDataService";
import MemberDataService from "../../services/MemberDataService";
import { apiConfig } from "../../config/api.config";

export default {
  name: "ClosedByAdjusterMonth",
  data() {
    return {
      selectedBranch: null,
      branches: [],
      adjusters: [],
      selectedAdjuster: null,
      selectedMonth: null,
      menuMonth: false,
      exporting: false,
    };
  },
  mounted() {
    this.loadBranches();
    this.retrieveAdjusters();
  },
  methods: {
    async loadBranches() {
      const res = await BranchDataService.getAll();
      this.branches = (res.data || []).sort((a, b) =>
        a.name.localeCompare(b.name)
      );
    },
    retrieveAdjusters() {
      MemberDataService.getAll()
        .then((response) => {
          this.adjusters = response.data
            .filter(
              (item) =>
                item.roles &&
                item.roles.some((b) => b.roleCode === "adjuster") &&
                item.active
            )
            .sort((a, b) => a.username.localeCompare(b.username));
        })
        .catch((e) => {
          console.log(e);
        });
    },
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          adjusterId: this.selectedAdjuster,
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const url =
          `${apiConfig.baseURL}/compliance/closed-by-adjuster/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
  },
  computed: {
    filteredAdjusters() {
      if (!this.selectedBranch) return [];
      const branchObj = this.branches.find((b) => b.id === this.selectedBranch);
      if (!branchObj) return [];
      const branchCode = branchObj.brCode;
      return this.adjusters
        .filter(
          (adj) =>
            Array.isArray(adj.branches) &&
            adj.branches.some((b) => b.brCode === branchCode)
        )
        .sort((a, b) => a.username.localeCompare(b.username));
    },
  },
};
</script>
